li{
    width: 40vh;
    text-align: center;
    margin: 15px auto;
    box-shadow: 3px 4px 18px -6px rgba(0,0,0,0.75);
    height: 5vh;
    background: white;
    line-height: 5vh;
    /*color: #0E4D92;*/
}

li:hover{
    color: orangered;
    box-shadow: 5px 4.5px 20px -6.5px rgba(0,0,0,0.75);
    width: 42vh;
    height: 5.5vh;
}


.faded{
    visibility: hidden;
    opacity: 0;
    transition: visibility 0s 2s, opacity 2s linear;
}